body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar li {
  display: inline-block;
}

.message {  
  text-justify: right;
  margin-right: 5px;
}

/* .scores {
float: right;

}  */
.wrapper {
  padding-top: 10px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 240px;
  margin: 1rem;
  position: relative;
  width: 270px;
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  /* box-shadow: 0 0px 0px #999, 0 0px 0px #999; */
  text-align: left;
  display: inline-block;
}

.card:hover {
  -webkit-transform: scale(1.00);
          transform: scale(1.00);
}

.imageBox {
  display: inline-block;


}

.card>.img-container {
  height: 100%;
  overflow: hidden;
  background: rgb(117, 117, 138);
}

.card>.img-container>img {
  width: 100%
}

.card>.content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card>.content>ul {
  list-style-type: none;
} 

.card>.content>ul>li {
  margin-top: 18px;
}
